.game {
    width: 600px;
    height: 200px;
    border: 1px solid black;
    margin: auto;
}

#dino {
    width: 50px;
    height: 50px;
    background-color: black;
    /* background-image: url();
    background-image: 50px 50px; */
    position: relative;
    top: 150px;
}

.jump {
    animation: jump 0.3s linear;
}

@keyframes jump {
    0% {
        top: 150px;
    }

    30% {
        top: 130px;
    }

    50% {
        top: 80px;
    }

    80% {
        top: 130px;
    }

    100% {
        top: 150px;
    }
}

#cactus {
    width: 20px;
    height: 40px;
    position: relative;
    top: 110px;
    left: 580px;

    background-color: green;
    /* background-image: url();
    background-size: 20px 40px; */

    animation: block 3s infinite linear;
}

@keyframes block {
    0% {
        left: 500px;
    }
    100% {
        left: -20px;
    }
}
